@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Rouble";
  font-display: swap;
  src: url("../fonts/rouble.woff2") format("woff2");
}
@font-face {
  font-family: "Chakra Petch";
  src: url("../fonts/ChakraPetch-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Chakra Petch";
  src: url("../fonts/ChakraPetch-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Chakra Petch";
  src: url("../fonts/ChakraPetch-Medium.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Chakra Petch";
  src: url("../fonts/ChakraPetch-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Chakra Petch";
  src: url("../fonts/ChakraPetch-Light.woff2") format("woff2");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  scroll-behavior: smooth;
}

html {
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 28px;
  font-family: "Chakra Petch", "Arial", sans-serif;
  color: #d1d1d1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #18181b;
}

a {
  color: inherit;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #18181b;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  /* stylelint-disable */
  font-weight: 400 !important;
  /* stylelint-enable */
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 120px;
}
@media (max-width: 1279px) {
  .container {
    padding: 0 50px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.header {
  z-index: 2;
  color: #ffffff;
  background-color: rgba(255, 0, 0, 0.1);
  transition: height 0.3s ease;
}
.header--sticky {
  position: sticky;
  top: 0;
  left: 0;
  width: 100%;
}
.header .header__for-blur {
  padding: 30px 0 32px;
  transition: height 0.3s ease, filter 0.3s ease;
}
@media (max-width: 1023px) {
  .header .header__for-blur {
    padding: 30px 0 20px;
  }
}
@media (max-width: 767px) {
  .header .header__for-blur {
    height: 93px;
    padding: 30px 0 10px;
  }
  .header .header__for-blur .container {
    height: 100%;
  }
}
.header .header__for-blur--blured {
  background-color: #18181b;
  filter: blur(5px);
}
.header__wave {
  position: absolute;
  top: 0;
  right: 0;
  width: 51.1111111111%;
  height: 354px;
  overflow: hidden;
  transition: height 0.3s ease, width 0.3s ease;
  pointer-events: none;
}
@media (max-width: 1023px) {
  .header__wave {
    width: 75%;
  }
}
@media (max-width: 767px) {
  .header__wave {
    width: 99%;
    height: 206px;
  }
}
.header__wave svg {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
}
.header--opaque {
  background-color: #18181b;
}
.header--opaque .header__wave {
  height: 100%;
}
@media (max-width: 767px) {
  .header--opaque .header__wave {
    height: 93px;
  }
}
.header .logo {
  flex-shrink: 0;
}
@media (max-width: 767px) {
  .header .logo {
    align-self: start;
    margin-top: 3px;
  }
}
.header__grid {
  display: grid;
  grid-template-columns: 106px 1fr;
  gap: 60px;
  align-items: start;
}
@media (max-width: 767px) {
  .header__grid {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    gap: 20px;
    height: 100%;
  }
}
.header__nav {
  display: flex;
  justify-content: end;
  align-items: start;
  gap: 20px;
}
@media (max-width: 767px) {
  .header__nav {
    display: flex;
    flex-grow: 1;
    flex-direction: column;
    align-items: stretch;
    overflow: hidden;
    visibility: hidden;
    opacity: 0;
    transition: visibility 0.3s ease, opacity 0.3s ease;
    gap: 10px;
  }
}
.header__list {
  display: flex;
  flex-grow: 1;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style: none;
  gap: 4px 18px;
}
@media (max-width: 767px) {
  .header__list {
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: start;
    overflow: auto;
  }
}
.header__item {
  flex-shrink: 0;
}
.header__link {
  display: block;
  padding: 11px 23px;
  transition: opacity 0.3s ease;
}
.header__link--gradient-border {
  border-width: 1px;
  border-style: solid;
  border-image: linear-gradient(90deg, #0cbaf1 0%, #e95ce9 100%);
  border-image-slice: 1;
}
@media (hover: hover) {
  .header__link:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.header__link.focus-visible:focus {
  opacity: 0.8;
}
.header__link.is-active {
  font-weight: 500;
}
@media (hover: hover) {
  .header__link.is-active:hover:not(.focus-visible) {
    cursor: default;
    opacity: 1;
  }
}
.header__link.is-active.focus-visible:focus {
  cursor: default;
  opacity: 1;
}
.header__toggle {
  display: none;
}
@media (max-width: 767px) {
  .header__toggle {
    position: absolute;
    top: 12px;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 32px;
    height: 32px;
    background: transparent;
    border-width: 1px;
    border-style: solid;
    border-image: linear-gradient(90deg, #0cbaf1 0%, #e95ce9 100%);
    border-image-slice: 1;
  }
}
.header.is-open .header__for-blur {
  height: 100vh;
  /* stylelint-disable-next-line */
  height: 100dvh;
  background-color: #18181b;
}
.header.is-open .header__nav {
  background-color: #18181b;
  visibility: visible;
  opacity: 1;
}
.header.is-open .header__toggle svg {
  display: none;
}
.header.is-open .header__toggle::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 18px;
  height: 2px;
  background-color: #ffffff;
  transform: translate(-50%, -50%) rotate(45deg);
}
.header.is-open .header__toggle::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 18px;
  height: 2px;
  background-color: #ffffff;
  transform: translate(-50%, -50%) rotate(-45deg);
}
.header.is-open .header__wave {
  height: 0;
}

.footer {
  position: relative;
  padding-top: 82px;
}
@media (max-width: 1023px) {
  .footer {
    padding-top: 40px;
  }
}
@media (max-width: 767px) {
  .footer {
    padding-top: 42px;
  }
}
.footer .logo {
  flex-shrink: 0;
  align-self: start;
  padding-top: 0;
}
@media (max-width: 1023px) {
  .footer .logo {
    padding: 6px 0;
  }
}
.footer__grid {
  display: flex;
  gap: 20px;
  padding-bottom: 33px;
}
@media (max-width: 1023px) {
  .footer__grid {
    display: grid;
    grid-template-columns: 2fr 2fr 3fr;
  }
}
@media (max-width: 767px) {
  .footer__grid {
    grid-template-columns: 4fr 5fr;
    padding-bottom: 8px;
  }
}
.footer__first-column {
  display: flex;
  flex-shrink: 2;
  flex-basis: 402px;
  flex-direction: column;
  justify-content: space-between;
  align-items: start;
  padding-bottom: 35px;
  gap: 40px;
}
@media (max-width: 1023px) {
  .footer__first-column {
    grid-column: 1/-1;
    flex-direction: row;
    align-items: center;
  }
}
.footer__socials {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}
@media (max-width: 1023px) {
  .footer__socials {
    justify-content: end;
  }
}
.footer__social-link {
  flex-shrink: 0;
  width: 46px;
  height: 46px;
  line-height: 0;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .footer__social-link:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.footer__social-link.focus-visible:focus {
  opacity: 0.8;
}
.footer__column {
  display: flex;
  flex-direction: column;
  gap: 16px;
  padding-top: 2px;
}
@media (max-width: 767px) {
  .footer__column {
    padding-bottom: 40px;
  }
}
.footer__column--second {
  flex-basis: 214px;
}
.footer__column--third {
  flex-basis: 198px;
}
@media (max-width: 767px) {
  .footer__column--fourth {
    grid-column: 1/-1;
  }
}
.footer__section-title {
  margin: 0;
  font-size: 16px;
  line-height: 28px;
  font-weight: 600;
  color: #ffffff;
}
.footer__links-list {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style-type: none;
  gap: 16px;
}
.footer__item {
  display: flex;
  align-items: center;
}
.footer__item svg {
  order: -1;
  margin-top: 6px;
}
.footer__link {
  display: flex;
  gap: 8px;
  color: inherit;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .footer__link:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.footer__link.focus-visible:focus {
  opacity: 0.8;
}
.footer__copy {
  position: relative;
  padding: 32px 0 40px;
  text-align: center;
}
@media (max-width: 767px) {
  .footer__copy {
    padding-bottom: 20px;
  }
}
.footer__copy::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background-image: linear-gradient(90deg, #0cbaf1 0%, #e95ce9 100%);
  opacity: 0.5;
}
.footer__waves {
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: -1;
  width: 70.2%;
  height: 100%;
  overflow: hidden;
}
@media (max-width: 1023px) {
  .footer__waves {
    width: 86%;
  }
}
@media (max-width: 767px) {
  .footer__waves {
    width: 100%;
    height: 314px;
  }
  .footer__waves svg {
    min-width: 637px;
  }
}

.btn {
  position: relative;
  display: inline-flex;
  padding: 18px 38px;
