  font-family: "Chakra Petch", "Arial", sans-serif;
  color: #ffffff;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 1;
  letter-spacing: 1.5px;
  text-transform: uppercase;
  background-image: linear-gradient(90deg, #0cbaf1 0%, #e95ce9 100%);
