.btn--transparent {
  border-width: 1px;
  border-style: solid;
  border-image: linear-gradient(90deg, #0cbaf1 0%, #e95ce9 100%);
  border-image-slice: 1;
  background: rgba(255, 255, 255, 0.06);
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}

.custom-input,
.custom-textarea {
  position: relative;
}
.custom-input__input-wrapper,
.custom-textarea__input-wrapper {
  position: relative;
  display: flex;
}
.custom-input__input-wrapper button,
.custom-textarea__input-wrapper button {
  position: absolute;
  top: 3px;
  right: 3px;
  bottom: 3px;
  width: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: opacity 0.3s ease;
  border: none;
  background: rgba(255, 255, 255, 0.21);
}
@media (hover: hover) {
  .custom-input__input-wrapper button:hover:not(.focus-visible),
.custom-textarea__input-wrapper button:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.custom-input__input-wrapper button.focus-visible:focus,
.custom-textarea__input-wrapper button.focus-visible:focus {
  opacity: 0.8;
}
.custom-input__icon,
.custom-textarea__icon {
  position: absolute;
  left: 14px;
  top: 50%;
  translate: 0 -50%;
  width: 24px;
  height: 24px;
}
.custom-input input,
.custom-input textarea,
.custom-textarea input,
.custom-textarea textarea {
  margin: 0;
  display: block;
  padding: 14px;
  width: 100%;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 28px;
  color: #ffffff;
  border: none;
  border-radius: 2px;
  background: rgba(255, 255, 255, 0.07);
  transition: opacity 0.3s ease;
}
