.article-preview__pic img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  user-select: none;
}
.article-preview__text {
  display: flex;
  flex-direction: column;
  gap: 6px;
  align-items: start;
  justify-content: start;
}
.article-preview__title {
  font-size: 12px;
  font-weight: 400;
  line-height: 20px;
  /* stylelint-disable-next-line */
  box-orient: vertical;
  -webkit-box-orient: vertical;
  /* stylelint-disable-next-line */
  display: -moz-box;
  /* stylelint-disable-next-line */
  display: -webkit-box;
  -webkit-line-clamp: 2;
  overflow: hidden;
}
.article-preview .property {
  display: block;
}

.aside {
  display: flex;
  flex-direction: column;
  gap: 64px;
}
.aside__title {
  margin-bottom: 24px;
  font-size: 20px;
  font-weight: 500;
  line-height: 1.3;
  color: #ffffff;
}
.aside ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.aside li {
  position: relative;
  padding: 23px 0;
}
.aside li:first-child {
  padding-top: 0;
}
.aside li:not(:last-child) {
  border-bottom: 1px solid rgba(176, 176, 176, 0.3);
}
.aside__hashtags {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
}

.sharp {
  z-index: -2;
  position: absolute;
  top: 170px;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
}
@media (max-width: 767px) {
  .sharp {
    top: 190px;
  }
}
.sharp--about-us {
  top: 974px;
}
@media (max-width: 767px) {
  .sharp--about-us {
    top: 1100px;
  }
}

